<template>
  <div>
    <div
      style="border: 1px solid #ccc; margin-top: 10px"
      :class="{
        'editor-is-edit': readOnlys
      }"
    >
      <!--  -->
      <Toolbar
        style="border-bottom: 1px solid #ccc"
        :editor="editor"
        :defaultConfig="toolbarConfig"
        mode="highest"
      />
      <!--  -->
      <Editor
        ref="editor"
        style="height: 400px; overflow-y: hidden"
        :defaultConfig="editorConfig"
        v-model="html"
        @onChange="onChange"
        @onCreated="onCreated"
        mode="highest"
      />
    </div>
  </div>
</template>

<script>
import { Editor, Toolbar } from "@wangeditor/editor-for-vue";
export default {
  components: { Editor, Toolbar },
  props: {
    content: {
      type: String,
      default: ""
    },
    readOnlys: {
      // 
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      mode: "highest",
      editor: null,
      html: "",
      toolbarConfig: {
        /*  */
        toolbarKeys: [
          "headerSelect",
          // '|',
          "bold",
          "underline",
          "italic",
          "color",
          "bgColor",
          // '|',
          "indent", // 
          "delIndent", // 
          "justifyLeft", // 
          "justifyRight", // 
          "justifyCenter", // 
          "justifyJustify", // 
          // '|',
          "fontSize",
          "fontFamily",
          "lineHeight",
          // '|',
          "bulletedList",
          "numberedList",
          "todo",
          "insertLink",
          "insertTable",
          // '|',
          "codeBlock",
          "divider",
          "uploadImage",
          "insertVideo",
          "undo",
          "redo"
        ]
        // excludeKeys: [ ], /*  */
      },
      editorConfig: {
        maxLength: 20000,
        placeholder: "",
        readOnly: false, // 
        //  MENU_CONF 
        MENU_CONF: {
          // 
          uploadImage: {
            customUpload: this.uploaadImg
          }
        }
      }
    };
  },
  watch: {
    readOnlys: {
      handler(newV) {
        if (newV) {
          this.editor.disable(); // 
        }
      }
    },
    content: {
      handler(v) {
        if (v) {
          const editor = this.$refs.editor;
          editor.editor && editor.editor.dangerouslyInsertHtml(v);
        }
      }
    }
  },
  mounted() {
    setTimeout(() => {
      console.log("this.content", this.content);
      const editor = this.$refs.editor;
      editor.editor && editor.editor.dangerouslyInsertHtml(this.content);
    }, 100);
  },
  methods: {
    async uploaadImg(file, insertFn) {
      if (file.size > 1024 * 1024 * 5) {
        this.$message.error("5");
        return;
      }
      let formdata = new FormData();
      formdata.append("file", file);
      formdata.append("securityLevel", 1);
      formdata.append("systemCode", "CSS");
      const res = await this.axiosPost(`/opb/api/file/upload`, formdata, {
        timeout: 20000
      });
      if (res.success) {
        insertFn(res.model.fileUrl);
      } else {
        this.$message.error(res.errorMessage);
      }
    },
    onCreated(editor) {
      this.editor = Object.seal(editor);
    },
    onChange(editor) {
      let data = {
        html: this.html,
        editor: this.$refs.editor
      };
      this.$emit("changeData", data);
    }
  },
  beforeDestroy() {
    const editor = this.editor;
    if (editor == null) return;
    editor.destroy(); //  editor
  }
};
</script>

